import React from "react";
import "./Quote.css";
import Swal from 'sweetalert2'

const Quote: React.FC = () => {
  // @ts-ignore
  const onSubmit = async (event) => {
    event.preventDefault();
    const formData = new FormData(event.target);
    formData.append("access_key", "0d09c72e-822c-478f-9a25-1c47631d58c8");
    const object = Object.fromEntries(formData);
    const json = JSON.stringify(object);
    const res = await fetch("https://api.web3forms.com/submit", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json"
      },
      body: json
    }).then((res) => res.json());

    if (res.success) {
      Swal.fire({
        title: "Success!",
        text: "Message sent successfully!",
        icon: "success"
      });
    }
  };

  return (
    <>
      <section className="quote">
        <div className="intro">
          <p>Fill out this form to discuss your new project.</p>
          <p>Put as many details as you feel comfortable.</p>
          <p>We will contact you shortly.</p>
        </div>
        <div>
          <form onSubmit={onSubmit}>
            <label className="required">Name</label>
            <div className="field-name">
              <input className="field required" type="text" placeholder="" name="name" required/>
            </div>
            <label className="required">Email</label>
            <input type="text" placeholder="" name="email" required/>
            <label>Phone</label>
            <input type="text" placeholder="" name="phone"/>
            <label className="required">Comment or message</label>
            <textarea name="message" required placeholder="" style={{height: "6rem", resize: "vertical"}}/>
            <button type="submit"
                    className=" bg-white px-8 py-3 border rounded my-4 hover:border-primary hover:text-primary transition-all w-[150px]">Submit
            </button>
          </form>
        </div>
      </section>
    </>
  )
}
export default Quote;
